import { useReducer, useEffect, useState } from "react";
import WebFormSelect from "./WebFormSelect";
import WebFormList from "./WebFormList";
import WebFormOptionSelect from "./WebFormOptionSelect";
import WebFormOptions from "./WebFormOptions";

const WebForm = ({ fields }) => {
  const [optionSelect, setOptionSelect] = useState(null);
  const [webFields, setWebFields] = useReducer((state, action) => {
    switch (action.type) {
      case "add":
        return state.map(field =>
          field.fieldName === action.payload.fieldName
            ? { ...field, fieldInForm: true }
            : field
        );
      case "remove":
        return state.map(field =>
          field.fieldName === action.payload.fieldName
            ? { ...field, fieldInForm: false }
            : field
        );
      case "addVisibleSelect":
        return state.map(field =>
          field.fieldName === action.payload.fieldName
            ? { ...field, fieldInForm: true, fieldFormVisible: true, pause: false }
            : field
        );

    //   case "pauseSelect": 
    //      return state.map(field =>Â field.fieldName === action.payload.fieldName ? {...field, pause: true} : field)

      case "unpauseSelect":
          return state.map(field => field.pause ? {...field, pause: false} : field)

      case "addHiddenSelect":
        return state.map(field =>
          field.fieldName === optionSelect[0].fieldName
            ? {
                ...field,
                fieldInForm: true,
                fieldFormVisible: false,
                pause: false,
                fieldOptions: field.fieldOptions.map(option =>
                  option.value === action.payload
                    ? { ...option, preselected: true }
                    : option
                )
              }
            : field
        );

      default:
        return state;
    }
  }, fields);

  const addNotSelect = e => {
    webFields.map(field =>
      field.fieldName === e.target.value
        ? field.fieldType !== "select"
          ? setWebFields({
              type: "add",
              payload: { fieldName: e.target.value }
            })
          : setWebFields({
              type: "pauseSelect",
              payload: { fieldName: e.target.value }
            })
        : null
    );
  };

  const addVisibleSelect = e => {
    setWebFields({
      type: "addVisibleSelect",
      payload: { fieldName: e.target.id }
    });
  };

  const addHiddenSelect = e => {
    setWebFields({
      type: "addHiddenSelect",
      payload: { optionValue: e.target.value }
    });
  };

  const removeFromList = e => {
    setWebFields({
      type: "remove",
      payload: { fieldName: e.target.id }
    });
  };

  useEffect(() => {
    console.log(webFields);
  }, [webFields]);

  return (
    <>
      <h3>Select field:</h3>
      <WebFormSelect
        webFields={webFields}
        addNotSelect={addNotSelect}
        // optionSelect={optionSelect}
      />
      <WebFormOptionSelect
        optionSelect={optionSelect}
        webFields={webFields}
        addVisibleSelect={addVisibleSelect}
        WebFormOptions={
          <WebFormOptions
            // optionSelect={optionSelect}
            addHiddenSelect={addHiddenSelect}
          />
        }
      />
      <h3>Selected fields:</h3>
      <WebFormList webFields={webFields} removeFromList={removeFromList} />
    </>
  );
};

export default WebForm;
